import { _fetch, host  } from './index';
// 
export const fetchSignParams = (url = window.location.href) => (
	_fetch(`${host}/codesquare/weixin/get-sign-package`, {
		body: {
			url
		}
	})
	// _fetch(`/wx/codesquare/weixin/get-sign-package`)
)
// 
export const fetchUserInfo = (token) => (
	_fetch(`${host}/codesquare/user/me`, {
		body: {
			token
		}
	})
);
// 
export const fetchGetTopic = (params) => (
	_fetch(`${host}/codesquare/topic/index`, {
		body: params,
	})
);
// 
export const fetchReviewTopic = (params) => (
	_fetch(`${host}/codesquare/topic/topic-detail`, {
		body: params,
	})
);
/**
 * flag === 1 
 * flag !== 1 
 * 
 */ 
export const fetchToggleLike = (flag = 1, topic_id, token) => {
	let target = flag === 1 ? 
		`${host}/codesquare/topic/cancel-like` : 
		`${host}/codesquare/topic/like`;

	return _fetch(target, {
		body: {
			topic_id,
			token,
		}
	});
}
/**
 * flag === 1 
 * flag !== 1 
 * 
 */ 
export const fetchToggleCollect = (flag = 1, topic_id, token) => {
	let target = flag === 1 ? 
		`${host}/codesquare/topic/cancel-collect` : 
		`${host}/codesquare/topic/collect`;

	return _fetch(target, {
		body: {
			topic_id,
			token,
		}
	});
}
// 
export const fetchCreateTopic = (params) => (
	_fetch(`${host}/codesquare/topic/topic-submit`, {
		body: params,
	})
);
// 
export const fetchReplyTopic = (params) => (
	_fetch(`${host}/codesquare/topic/reply`, {
		body: params,
	})
)
// 
export const fetchGetReply = (params) => (
	_fetch(`${host}/codesquare/list/reply`, {
		body: params,
	})
)
// 
export const fetchGetUserPublish = (token) => (
	_fetch(`${host}/codesquare/topic/publish`, {
		body: {token}
	})
)
// 
export const fetchGetUserFav = (token) => (
	_fetch(`${host}/codesquare/user/fav-list`, {
		body: {token}
	})
)
// 
export const fetchGetUserReply = (token) => (
	_fetch(`${host}/codesquare/user/bereplied-list`, {
		body: {token}
	})
)



