import React from 'react';
import { Route, Redirect, IndexRedirect } from 'react-router';
import App from './components/App';
import Home from './components/Home';
import Square from './components/Square';
import User from './components/User';
import Ranking from './components/Ranking';
import Market from './components/Market';

import Subject from './components/Subject';
import CreateSubject from './components/CreateSubject';
import ReviewSubject from './components/ReviewSubject';
import Reply from './components/Reply';

import Favorite from './components/Favorite';
import ReplyList from './components/ReplyList';

// 
export const routeMap = {
	'/home/square': '',
	'/home/ranking': '',
	'/home/market': 'M',
	'/home/user': '',

	'/subject/create': '',
	'/subject/review': '',
	'/subject/reply': '',

	'/user/public': '',
	'/user/favorite': '',
	'/user/replyList': '',
}

export default (
	<Route path="/" component={App}>
		<IndexRedirect to="/home/square" />
		<Route path="home" component={Home}>
			<Route path="square" component={Square} />
			<Route path="ranking" component={Ranking} />
			<Route path="market" component={Market} />
			<Route path="user" component={User} />
		</Route>
		<Route path="subject" component={Subject}>
			<Route path="create" component={CreateSubject} />
			<Route path="review/:id" component={ReviewSubject} />
			<Route path="reply/:id" component={Reply} />
		</Route>
		<Route path="user">
			<Route path="public" component={Favorite} />
			<Route path="favorite" component={Favorite} />
			<Route path="replyList" component={ReplyList} />
		</Route>
		<Redirect from="*" to="/home/square" />
	</Route>
);


